import { ITelemetry } from '@msdyn365-commerce/telemetry-internal';
import { IActionContext } from '../action';
import { ICultureInfoFormatter } from '../ICultureFormatter';
import { IRequestContext } from './IRequestContext';

export interface ICoreContext<TApp extends {} = {}> {
    actionContext: IActionContext;
    // module telemetry
    telemetry: ITelemetry;
    /**
     * The following prop exists for internal telemetry but does not get exposed on the definitions
     * internalTelemetry: IInternalTelemetry;
     */
    request: IRequestContext;
    cultureFormatter: ICultureInfoFormatter;
    app: TApp;
}

export interface IWithContext<TApp extends {} = {}> {
    telemetry: ITelemetry;
    context: ICoreContext<TApp>;
}
